import * as React from "react"
import {useState,useEffect} from 'react'
import HomeBanner from "../components/HomeBanner"
import Layout from "../components/Layout"
import TechList from "../components/TechList"
import { graphql, Link, useStaticQuery } from 'gatsby'



// PICTURES

// import prabhi from '../images/prabhi.png'
import workOne from '../images/1.png'
import workTwo from '../images/2.png'
import workThree from '../images/3.png'
import Contact from "../components/Contact"

// markup

const HomeAbout = () => {
  return (
    <div className = 'home-about'>
      <div className = 'home-about-content'>
        <div className = 'prabhi-pic'>
          {/* <img src = {prabhi} /> */}
        </div>
        <div className = 'home-about-txt'>
          <div>
            <h2>
            Let me Introduce Myself
            </h2>
            <p>
            My name is Prabhsharan Gosal (Prabhi).  I previously worked as a freelancer with some small businesses and helped them build high-quality and effective websites and web applications focusing on user engagement, user experience, and performance. I specialize in working with JavaScript and React.             
            </p>
          </div>
          <div className = 'button-group'>
            <div className = 'btn btn-light'>
                <Link to = '/work'>
                    Download my Resume
                </Link>
            </div>
            <div className = 'btn btn-dark'>
                <Link to = '/#contact'>
                    Know more about me
                </Link>
            </div>
          </div>
          {/* <TechList /> */}
        </div>
        
      </div>
    </div>
  )
}

const HomeWork = () => {

  const [projects,setProjects] = useState(useStaticQuery(graphql`
  query{
      allContentfulProject {
          edges {
          node {
              slug
              tech
              title
              duties
              hex
              images{
                file{
                  url
                }
              }
              }  
          }
      }
  }
`))

  // useEffect(() => {
  //   const data = 
  //   setProjects(projects.push(data.allContentfulProject.edges))
    console.log(projects)
  // })

  const revList = projects.allContentfulProject.edges.reverse()
  
  return (
    <div className = 'home-work'>
      <div className = 'home-work-content'>
        <div>
          <span>PORTFOLIO</span>
          <h2>My Work</h2>
        </div>
        <div className = 'home-work-grid'>
          {revList.map(edge => {
            return(
              <div style = {{background:edge.node.hex}} className = 'home-work-grid-card'>
                <div className = 'home-grid-txt'>
                  <h3>{edge.node.title}</h3>
                  <h5>Tech Stack</h5>
                  <p>{edge.node.tech}</p>
                    <div className = 'btn btn-light'>
                        <Link to = {`/work/${edge.node.slug}`}>
                            View Project
                        </Link>
                    </div>
                </div>
                <div className = 'home-grid-img'>
                  <img src = {edge.node.images[0].file.url}></img>
                </div>
              </div>
            )
          })}
        </div>
        <div className = 'home-work-link'>
          <div className = 'btn btn-dark'>
              <Link to = '/work'>
                  View More
              </Link>
          </div>
        </div>
      </div>
    </div>
  )
}

const IndexPage = ({location}) => {

  // console.log(location.pathname)

  return (
    <Layout location = {location.pathname}>
      <HomeBanner />
      <HomeAbout />
      <HomeWork />
      <Contact />
    </Layout>
  )

}

export default IndexPage
